import React, { useState } from "react";
import './first-information.style.css';
import DotFooter from '../dot-footer/dot-footer.component';
import Dialog from '../dialog/dialog.component';
import TextDialog from '../textInput-dialog/textInput-dialog.component';
import { useStore } from 'react-redux';
import Fade from 'react-reveal/Fade';

const gender_values = ['', ''];

const married_values = ['', '', ''];
const blood_type = ['+A', '+B', '+AB', '+O', '-A', '-B', '-AB', '-O'];
const education_values = ['', '', ' ', '', ' ', ''];
const state_values = [
    '', '', ' ', '', '',
    '', ' ', '', '', '',
    '', '', '', '', '',
    ' ', '', '', '', '',
    '', '', '', '  ', '',
    '  ', '', '  ', ' ', ' ',
    ''];


const FirstInformation = () => {
    const [name, setName] = useState('');
    const [number, setNumber] = useState('');
    const [gender, setGender] = useState('');
    const [married, setMarried] = useState('');
    const [state, setState] = useState('');
    const [cities, setCities] = useState('');
    const [education, setEducation] = useState('');
    const [age, setAge] = useState('');
    const [blood, setBlood] = useState('');
    const [height, setHeight] = useState('');
    const [weight, setWeight] = useState('');
    const [step, setStep] = useState(0);
    const city = useStore().getState().CityReducer;



    const stepComponent = [
        <>
            <TextDialog state={name} setState={setName} items={[]} title={'   '} />
            <TextDialog state={number} setState={setNumber} items={[]} title={' '} />
            <p className="login-copy" style={{ color: '#b71c1c', cursor: 'pointer' }}
                onClick={() => setStep((oldState) => oldState + 1)}
            >      </p></>,



        <>
            
            <Dialog state={state} setState={setState} items={state_values} title={''} />
            <Dialog state={cities} setState={setCities} items={city[state] ? city[state] : []} title={''} />
            <Dialog state={education} setState={setEducation} items={education_values} title={''} />

        </>,





        <>
           
            <Dialog state={gender} setState={setGender} items={gender_values} title={''} />
            <Dialog state={married} setState={setMarried} items={married_values} title={' '} />

            <TextDialog state={age} setState={setAge} items={[]} title={' '} />



        </>,

        <>
            
            <Dialog state={blood} setState={setBlood} items={blood_type} title={' '} />
            <TextDialog state={height} setState={setHeight} items={[]} title={''} />
            <TextDialog state={weight} setState={setWeight} items={[]} title={''} />


        </>,


    ];


    return (

        <div id="first-information">


            <div className="login-form">
                <div className="login-heading-container">
                    <h3 className="login-heading" style={{ direction: 'rtl', fontFamily: 'Samim', color: 'black' }}>
                        </h3>
                </div>
                <div style={{ minHeight: '320px' }}>
                <p className="login-copy">     </p>
                    <Fade spy={step} duration={400}>
                        {stepComponent[step]}
                    </Fade>
                </div>
                {/* {step === 3 ?
                    <button className="end-form-button" style={{ background: '#0094CC' }} ></button> :
                    null} */}

                <DotFooter activeStep={step} setActiveStep={setStep} totalStep={4} />

            </div>
        </div>


    )

};



export default FirstInformation;