import './form.style.css';
import React, { useState } from "react";
import Dialog from '../dialog/dialog.component';
import { useStore } from 'react-redux'
import TextDialog from '../textInput-dialog/textInput-dialog.component';
import axios from 'axios';

const gender_values = ['', ''];
const married_values = ['', '', ''];
const blood_type = ['+A', '+B', '+AB', '+O', '-A', '-B', '-AB', '-O'];
const education_values = ['', '', ' ', '', ' ', ''];
const state_values = [
    '', '', ' ', '', '',
    '', ' ', '', '', '',
    '', '', '', '', '',
    ' ', '', '', '', '',
    '', '', '', '  ', '',
    '  ', '', '  ', ' ', ' ',
    ''];

const Form = ({ disabled, user }) => {
    const [name, setName] = useState(user.full_name);
    const [gender, setGender] = useState(user.sex);
    const [married, setMarried] = useState(user.married);
    const [state, setState] = useState(user.province.title);
    const [cities, setCities] = useState(user.city.title);
    const [education, setEducation] = useState(user.education);
    const [age, setAge] = useState('21');//user.???
    const [blood, setBlood] = useState(user.blood);
    const [height, setHeight] = useState(user.height);
    const [weight, setWeight] = useState(user.weight);
    const [mobile, setMobile] = useState(user.mobile);
    const store = useStore();
    const city = store.getState().CityReducer;









    const sendProfile = () => {




        const headers = {
            'Content-Type': 'application/json',
            
            'Authorization': `Bearer ${JSON.parse(localStorage.getItem('myBeLovedToken'))}`,


        }
        const data = {
            full_name: name,
            sex: gender,
            city: cities,
            province: state,
            married: married,
            education: education,
            height: height,
            weight: weight,
            blood: blood,
            birthday: user.birthday,
            mobile: mobile
        }


        const url = 'http://185.55.226.171/api/profile';
        const proxyurl = "https://cors-anywhere.herokuapp.com/";

        axios.post( url, data, {
            headers: headers
        }).then((response) => {
            store.dispatch({type:'SET_CURRENT_USER',payload:data})
        })
        .catch((error)=>{
            console.log(error.response)
        })




    }







    useStore().dispatch({ type: 'SET_GLOBAL_FUNCTION', payload: sendProfile })





    return (


        <div style={{ direction: 'rtl' }}>


            <TextDialog state={name} setState={setName} items={[]} title={'   '} disabled={disabled} />
            <TextDialog state={mobile} setState={setMobile} items={[]} title={' '} disabled={disabled} />
            <Dialog state={gender} setState={setGender} items={gender_values} title={''} disabled={disabled} />
            <Dialog state={blood} setState={setBlood} items={blood_type} title={' '} disabled={disabled} />
            <Dialog state={state} setState={setState} items={state_values} title={''} disabled={disabled} />
            <Dialog state={cities} setState={setCities} items={city[state] ? city[state] : []} title={''} disabled={disabled} />
            <Dialog state={married} setState={setMarried} items={married_values} title={' '} disabled={disabled} />
            <Dialog state={education} setState={setEducation} items={education_values} title={''} disabled={disabled} />
            <TextDialog state={height} setState={setHeight} items={[]} title={''} disabled={disabled} />
            <TextDialog state={weight} setState={setWeight} items={[]} title={''} disabled={disabled} />
            <TextDialog state={age} setState={setAge} items={[]} title={' '} disabled={disabled} />





        </div>

    )
}


export default Form;


